"use client"

import { Glasses, Zap, Watch, Camera, Brain, Speaker } from "lucide-react"
import Link from "next/link"
import Image from "next/image"

const products = [
  {
    icon: Glasses,
    title: "Meta Quest 3",
    description: "Next-gen VR headset with mixed reality and AI-powered features",
    price: "$499",
    color: "from-blue-500 to-cyan-500",
    link: "/products/meta-quest-3",
    image: "/meta_quest1.webp",
  },
  {
    icon: Zap,
    title: "Meta AI Glasses",
    description: "Ray-Ban Meta smart glasses with AI assistant and hands-free interaction",
    price: "$299",
    color: "from-cyan-500 to-teal-500",
    link: "/products/meta-ai-glasses",
    image: "/meta_glasses1.webp",
  },
  {
    icon: Watch,
    title: "Apple Watch Ultra 3",
    description: "Advanced smartwatch with AI health tracking and fitness features",
    price: "$799",
    color: "from-teal-500 to-green-500",
    link: "/products/apple-watch-ultra-3",
    image: "/watch1.png",
  },
  {
    icon: Camera,
    title: "Google Nest Cam",
    description: "AI-powered security camera with intelligent alerts",
    price: "$179",
    color: "from-green-500 to-emerald-500",
    link: "/products/google-nest-cam",
    image: "/google_cam1.png",
  },
  {
    icon: Brain,
    title: "Amazon Echo Hub",
    description: "Smart home display with Alexa AI assistant",
    price: "$179",
    color: "from-emerald-500 to-cyan-500",
    link: "/products/amazon-echo-hub",
    image: "/amazon1.png",
  },
  {
    icon: Speaker,
    title: "Apple HomePod",
    description: "High-fidelity speaker with Siri intelligence",
    price: "$299",
    color: "from-cyan-500 to-blue-500",
    link: "/products/apple-homepod",
    image: "/homepod1.png",
  },
]

export default function AIProducts() {
  return (
    <section id="ai-products" className="py-20 px-4 sm:px-6 lg:px-8 bg-background">
      <div className="max-w-7xl mx-auto">
        <div className="mb-16 text-center space-y-4">
          <h2 className="text-4xl md:text-5xl font-bold text-foreground">
            Smart Home <span className="text-accent">Collection</span>
          </h2>
          <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
            Cutting-edge AI-powered smart home devices engineered for convenience and security
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {products.map((product, index) => {
            const Icon = product.icon
            return (
              <Link
                key={index}
                href={product.link}
                className="group bg-card border border-border rounded-xl overflow-hidden hover:border-accent/50 transition duration-300 hover:shadow-lg hover:shadow-accent/20 block"
              >
                {/* Product Image or Icon */}
                {product.image ? (
                  <div className="relative w-full h-48 bg-gradient-to-br from-accent/5 to-accent/10">
                    <Image
                      src={product.image}
                      alt={product.title}
                      fill
                      className="object-contain p-4 group-hover:scale-105 transition duration-300"
                    />
                  </div>
                ) : (
                  <div className="p-6 pb-0">
                    <div
                      className={`w-12 h-12 bg-gradient-to-br ${product.color} rounded-lg p-2.5 mb-4 group-hover:scale-110 transition`}
                    >
                      <Icon className="w-full h-full text-white" />
                    </div>
                  </div>
                )}

                {/* Content */}
                <div className="p-6">
                <h3 className="text-xl font-bold text-foreground mb-2">{product.title}</h3>
                <p className="text-muted-foreground mb-4 text-sm leading-relaxed">{product.description}</p>

                {/* CTA */}
                <div className="flex justify-end pt-4 border-t border-border">
                  <span className="px-4 py-2 bg-accent/20 group-hover:bg-accent text-accent group-hover:text-primary-foreground rounded-lg transition text-sm font-medium">
                    Check It Out
                  </span>
                </div>
                </div>
              </Link>
            )
          })}
        </div>
      </div>
    </section>
  )
}
